import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyAlphaNum, onlyNumber, WindowComponent } from 'summer';
import { Atestado, CLASS_NAME } from '../../../../models/dtos/mfp/atestado';
import { ORGAO_CLASSE_ENUM_VALUES } from '../../../../models/enumerated/mfp/orgaoClasseEnum';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface AtestadoEditViewProps extends EditViewProps<Atestado> {}

const labelWidth: number = 200;

const AtestadoEditView: FC<AtestadoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Atestado>({ defaultValues: { nrAtestado: 0, diasAfastamento: 1, ...selectedItem }, mode: 'onBlur' });

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().cid && formProps.getValues().cid.length < 3) {
      erros.push('O código CID deve ter no mínimo 3 caracters');
    }

    return erros;
  };

  return (
    <WindowComponent title="Manutenção de Atestado Médico">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 9) } }}
              id="nrAtestado"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Número Atestado"
              placeholder="Informe o Número do Atestado."
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 4 } }}
              id="cid"
              isPrependLabel={true}
              label="Código CID"
              labelWidth={labelWidth}
              placeholder="Informe o Dígito Verificador do Banco."
              tooltipProps={{ message: 'Classificação Internacional de Doenças - CID' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
              id="medicoEmitente"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Emitente"
              placeholder="Informe o Emitente."
              tooltipProps={{ message: 'Nome do médico/dentista que emitiu o atestado.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="dtEmissao"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Data Emissão"
              placeholder="Informe a Data da Emissão"
              type={InputType.DATE}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { max: 999, min: 1 } }}
              id="diasAfastamento"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Dias de Afastamento"
              placeholder="Informe os Dias de Afastamento"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="orgaoClasse"
              label="Orgão de Classe"
              labelWidth={labelWidth}
              isPrependLabel={true}
              placeholder="Selecione o Orgão de Classe"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: ORGAO_CLASSE_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 14), required: true } }}
              id="nrOrgaoClasse"
              isPrependLabel={true}
              labelWidth={labelWidth}
              label="Número de Inscrição"
              placeholder="Informe o Número de Inscrição."
            />
          </Col>
          <Col>
            <CepUFIS
              formProps={{ ...formProps, validation: { required: true } }}
              label="UF Orgão de Classe"
              placeholder="Informe a UF do Orgão de Classe"
              id="ufOrgaoClasse"
              labelWidth={labelWidth}
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: false } }}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default AtestadoEditView;
